.projects-body h1 {
    /* color: rgb(0, 0, 0);
      color: rgb(0, 255, 234); */
      font-size: 2.5rem;
      font-family: 'Jockey One', sans-serif;
      color: #fff;
      text-align: center;
    }
    .project-name{
        font-size: 1rem;
        margin: 1em!important;
        font-family: 'Jockey One', sans-serif;
        color: #fff;
    }
.card {
    color: rgb(0, 255, 234) !important;
    font-family: "Jura", sans-serif;
    background-color: rgb(20, 20, 20) !important;
    border-radius: 10px!important;
    border: 1px solid rgb(0, 255, 234) !important;
    margin: 1rem !important;
    box-shadow: 0px 1px 10px rgb(0, 255, 234) inset, 0px 10px 15px rgba(0, 0, 0, 0.363)!important;
  
  }
  .card:hover{
    color: rgb(0, 255, 234)!important;
    background-color: #000000!important;
    border-color: #8ad3d3;
    box-shadow: 0px 0px 6px rgb(0, 255, 234) inset, 0px 0px 10px rgb(255, 174, 0)!important;
  
  }

  .project-decription p{
    font-family: "Jura", sans-serif;
    font-size: 1rem;
  }
  
  .github{
    height:3em;
  }

  .deployLink{
    text-decoration: none;
    font-size: 1.3rem;
    font-family: 'Jockey One', sans-serif;
    color: #fff;
  }